@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  text-decoration: none;
}

body {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  color: #fdfdfd;
}

#Home-Page {
  height: 100vh;
  width: 100%;
}

.Header {
  background: #202024;
  display: flex;
  justify-content: space-around;
  padding: 1.5em;
  z-index: 1;
  width: 100%;
}

.Nav-Bar {
  width: 39%;
}

.Nav-Bar ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.Nav-Bar ul a {
  font-weight: 500;
  color: #fdfdfd;
  text-decoration: none;
  transition: all 0.1s ease-out;
}

.Nav-Bar ul a:hover {
  color: #5d2de2;
}

.Main-Content {
  display: flex;
  justify-content: space-around;
  height: 75%;
  width: 100%;
}

.Main-Content .Contacte-Me-Container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55%;
}

.Introduction {
  font-size: 1.5em;
}

.Student-Web {
  font-weight: 500;
}

.Main-Content .Blob-svg {
  display: flex;
  align-items: center;
  height: 100%;
  width: 45%;
}

.Main-Content .Blob {
  position: relative;
  z-index: 0;
  height: 100%;
  display: flex;
  width: 100%;
}

.Container-Buttons-Contact {
  margin-top: 0.5em;
  margin-left: 8em;
  position: absolute;
  width: 15%;
}

.Btn-Contact {
  background: #e3d42d;
  font-size: 1em;
  color: #202024;
  cursor: pointer;
  font-weight: 600;
  outline: none;
  border: none;
  border-radius: 0.3em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1em;
  height: 2.5em;
  width: 100%;
}

.Btn-Contact:hover {
  background: #ffef3c;
}

.Btn-Whatsapp {
  background: #dbcd33;
}

.Btn-LinkedIn {
  background: #e3d42d;
}

.Btn-GitHub {
  background: #dbcd33;
}

.whats {
  margin-left: 0.3em;
}

.Scrool-Text {
  margin-top: -1em;
  margin-bottom: 0.5em;
}

.seta {
  color: #5d2de2;
  height: 5.5em;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.Mouse-Seta {
  color: #5d2de2;
}

.App {
  background: #121214;
  width: 100%;
}

#About-Me {
  background: #202024;
  display: flex;
  flex-flow: row nowrap;
  height: 88vh;
  width: 100%;
}

.Avatar-Matheus {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  color: #5d2de2;
  width: 50%;
}

.Avatar-Matheus-Img {
  border: 3px solid #5d2de2;
  border-radius: 50%;
  width: 40%;
}

.Icons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  width: 100%;
}

.About-Me-Container {
  display: flex;
  align-items: center;
  width: 50%;
}

.About-Me-Center {
  width: 85%;
}

.Abstract-About-Me {
  text-align: start;
}

.Abstract-About-Me h2 {
  font-weight: 500;
}

.Abstract-About-Me p {
  font-weight: 400;
  line-height: 1.5em;
}

.About-Me-Btn-LinkedIn {
  margin: 3em 0;
  color: #fdfdfd;
  background: #5d2de2;
  transition: 0.1s all ease-out;
  height: 3em;
  width: 30%;
}

.About-Me-Btn-LinkedIn:hover {
  background: #351980;
}

#Projects {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  height: 100vh;
  width: 100%;
}

.Title-Projects {
  font-weight: 500;
  text-align: center;
  padding: 1em;
  width: 100%;
}

.Projects-Container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  width: 83%;
}

.Project {
  text-align: start;
  background: #202024;
  margin: 1em;
  padding: 2em 1.5em;
  font-size: 0.9em;
  border-radius: 0.3em;
  height: 15em;
  width: 30%;
}

.Title-Project {
  font-weight: 400;
  margin-bottom: 0.5em;
  height: 17%;
  width: 100%;
}

.Abstract-Project {
  font-weight: 500;
  height: 55%;
  width: 100%;
}

.Buttons-Project {
  display: flex;
  justify-content: space-between;
  height: 25%;
  width: 100%;
}

.Btn-Project-Links {
  background: #5929cf;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  border: none;
  border-radius: 0.3em;
  cursor: pointer;
  color: #fdfdfd;
  transition: 0.1s all ease-out;
  height: 2.3em;
  width: 45%;
}

.Btn-Project-Links:hover {
  background: #351980;
}

.Repositories-On-GitHub {
  text-align: center;
  padding: 1em;
  width: 100%;
}

.Link-Repositories-On-GitHub {
  color: #5929cf;
  transition: 0.1s all ease-out;
}

.Link-Repositories-On-GitHub:hover {
  color: #754be7;
}

#Services {
  background: #202024;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  height: 88vh;
  width: 100%;
}

.Services-Container-Center {
  font-size: 1em;
  display: flex;
  width: 80%;
}

.Services-Title {
  font-size: 2em;
  font-weight: 400;
  text-align: center;
  margin-bottom: 1.4em;
  width: 100%;
}

.Service {
  background: #121214;
  text-align: center;
  border-radius: 0.3em;
  font-size: 0.9em;
  padding: 1em;
  margin: 1em;
  height: 20em;
  width: 25%;
}

.Icon-Service {
  color: #5429cf;
  text-align: center;
  padding: 2.2em 0;
}

.Title-Service {
  margin-bottom: 0.8em;
  font-weight: 400;
}

.Text-Service {
  font-weight: 600;
  line-height: 1.5em;
}

#Skills {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  height: 88vh;
  width: 100%;
}

.Skills-Container {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 80%;
}

.Abstract-Skills {
  line-height: 1.5em;
  padding-right: 4em;
  width: 50%;
}

.Skills-Title {
  margin-bottom: 1em;
  font-size: 1.8em;
  font-weight: 500;
}

.Abstract-Skills-Text {
  font-weight: 500;
}

.Icons-Skills {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  width: 50%;
}

.Skill-Card {
  border: 4px solid #202024;
  border-radius: 0.3em;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.397);
  background: #202024;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  color: #5429cf;
  padding: 1.4em 0.9em;
  margin: 0.5em;
  height: 9em;
  width: 20%;
}

.Text-Skill {
  display: none;
  margin-top: 0.5em;
  font-weight: 500;
}

.Skill-Card:hover {
  border-bottom: 4px solid #5429cf;
}


.Footer {
  background: #5429cf;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  width: 100%;
}

.Footer-Icons-Container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  width: 50%;
}

.Icons-Footer {
  display: flex;
  justify-content: space-around;
  margin-right: 4em;
  width: 30%;
}

.Icon-Back-To-Home {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
}

.Back-To-Home {
  background: #4622aa;
  border: 2px solid #4622aa;
  border-radius: 0.2em;
  cursor: pointer;
  color: #fdfdfd;
  transition: 0.1s all ease-out;
}

.Icon-Footer {
  color: #fdfdfd;
  transition: 0.1s all ease-out;
}

.Icon-Footer:hover {
  color: #e3d42d;
}

.Back-To-Home:hover {
  border: 2px solid #351980;
  background: #351980;
}

